// 
$body-bg: #fcfcfc;

// 
$font-size-root: 14px;
// 
$font-size-xl: 1.5rem; // ~21px

// 
$spacer-sm: .5714rem; // ~8px
// 
$spacer-xs: .3571rem; // ~5px

// 
$bg-opacity: .5;
// 
$bg-opacity-light: .6;
// 
$bg-opacity-lighter: .7;
// 
$bg-opacity-lightest: .95;

// 
$list-group-bg: rgba(#fff, $bg-opacity);
// 
$list-group-hover-bg: rgba(darken(#f5f5f5, 5%), $bg-opacity);

// 
$breadcrumb-bg: rgba(darken(#eceeef, 5%), $bg-opacity);

// 
$nav-tabs-active-link-hover-bg: rgba($body-bg, $bg-opacity);
// 
$nav-tabs-link-hover-border-color: darken(#eceeef, 3%);

// 
$card-bg: rgba(#fff, $bg-opacity);
// 
$card-cap-bg: rgba(darken(#f5f5f5, 2%), $bg-opacity);

// 
$pagination-bg: rgba(#fff, $bg-opacity-light);
// 
$pagination-disabled-bg: rgba(#fff, $bg-opacity-light);

// 
$input-bg: rgba(#fff, $bg-opacity-light);
// 
$custom-select-bg: rgba(#fff, $bg-opacity-light);

// 
$modal-header-border-color: darken(#e5e5e5, 1%);

// 
$blockquote-bg: rgba(darken($body-bg, 6%), $bg-opacity-lighter);
// 
$blockquote-border-width: .3rem;
// 
$blockquote-border-color: darken($body-bg, 10%);