body {
  padding-top: 3.375rem; // ~47px
}

// 
body, .modal-content {
  background-image: url("../img/bg/1.jpg");
}

// 
#successPage, #errorPage, #loginPage, #registerPage {
  padding-top: 0;
}

// 
main {
  margin-bottom: $spacer*1.5;
}

// 
#mainMenu {
  overflow-y: auto;
}

// 
#userMenuDropdownBtn {
  max-width: 178px;
  white-space: nowrap;
  overflow: hidden;
}

// 
.main-footer {
  font-size: $font-size-sm;
  text-align: center;
  background-color: rgba(darken($body-bg, 10%), $bg-opacity);

  // 
  .version {
    color: darken($brand-warning, 10%);
  }
}

// /
#rollToTopOrBottom {
  position: fixed;
  right: 12px;
  bottom: 32px;
  padding: 12px;
  color: #fff;
  line-height: 1rem;
  background-color: #000;
  border-radius: $border-radius;
  cursor: pointer;
  opacity: .15;

  &:hover {
    opacity: .35;
  }
}

// /
#loginPage, #registerPage {
  #rollToTopOrBottom {
    display: none;
  }
}